#background {
  background: linear-gradient(186deg, #52c6f8, #e23ce2);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  z-index: -1
}

#controls {
  position: absolute;
  top: calc(100% - 200px);
  right: 50px;
  width: 300px;
  padding: 2rem 3rem;
  z-index: 1;
  background: #f4f4f4;
  border-radius: 5px;
  box-shadow: 0 10px 40px #000;
  transition: all 250ms ease-in;
}
@media (min-width: 768px) {
#controls {
  top: 40px;
}
}
.control {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  font: bold 1rem sans-serif;
  color: #66696b;
}
.control-label {
  text-transform: uppercase;
}
.control-value {
  text-align: right;
}

#stats {
  font: bold 1em sans-serif;
  position: absolute;
  top: 3%;
  left: 5%;
  width: auto;
  z-index: 1;
}
.lib {
  opacity: 0.6;
  border-bottom: 5px solid black;
  max-height: 75px;
}
.lib-name {
  font-size: 75px;
}
.lib-version {
  font-size: 18px;
  padding-left: 0.5em;
}
.fps {
  font-size: 150px;
  opacity: 0.2;
}
.stat {
  font-size: 40px;
  opacity: 0.2;
}

#triangle {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.triangle {
  fill: transparent;
  stroke: FloralWhite;
  stroke-width: 3;
}
.triangle-text {
  font: bold 40px sans-serif;
}

input[type="range"] {
  -webkit-appearance: none;
  width: 10rem;
  height: 0.75rem;
  border-radius: 0.75rem;
  background: #d7dcdf;
  outline: none;
  padding: 0;
  margin: 0;
  cursor: ew-resize;
}
